img {
  vertical-align: middle;
  border: none;
}
.slider-wrapper {
  width: 100%;
  position: relative;
  overflow: hidden;
  height: 230px;
}
.slider-wrapper .scroller {
  height: 100%;
  flex-wrap: nowrap;
}
.h2-swiper .scroller {
  display: flex;
}
.slider-wrapper .scroller .item {
  background: #ccc;
}
.h2-swiper .scroller .item {
  min-height: 1px;
  width: 100%;
  flex-shrink: 0;
}
.item-wrapper {
  position: relative;
  overflow: hidden;
}
.item-wrapper img {
  height: 100%;
  display: block;
  width: 100%;
}
