body {
  color: #999;
  font-size: 14px;
}
html {
  font-size: 100px;
}
.slider-wrapper {
  height: 190px;
  overflow: hidden;
}
.cate-all {
  display: flex;
  flex-wrap: wrap;
  background: #fff;
  transform: scale(1);
  padding: 17px 0 18px;
}
.slider-wrapper {
  width: 100%;
  position: relative;
}
.cate-all .scroller {
  min-width: 100%;
  display: -webkit-box;
  padding-bottom: 24px;
  box-sizing: content-box;
  overflow: auto;
}
.slider-wrapper .scroller {
  height: 100%;
  flex-wrap: nowrap;
}
.slider-wrapper .scroller > .item-col:first-child {
  padding-left: 7px;
}
.cate-all .item-col {
  display: flex;
  flex-direction: column;
  width: 0.7rem;
}
.cate-all .cate-item {
  display: inline-flex;
  float: left;
  margin: 0 0 18px;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cate-all .cate-item img {
  width: 50px;
  height: 50px;
}
.cate-all .cate-item p {
  font-size: 12px;
  color: #333;
  font-weight: 300;
  line-height: 1;
  margin-top: 12px;
}
.cate-all .pagination {
  margin: 0 auto;
  text-align: center;
  bottom: 22px !important;
  height: 1.5px;
  width: 40px;
  background: #e4e4e4;
  overflow: hidden;
}
.h2-swiper .pagination {
  position: relative;
  left: 0;
  justify-content: center;
}
.cate-all .pagination .inner {
  width: 50%;
  background: #0c9;
  position: absolute;
  top: 0;
  height: 1.5px;
}
