.box {
  margin: 0 0.12rem;
}
.content {
  margin-top: 21px;
}
.title {
  display: flex;
  align-items: baseline;
  margin-bottom: 0.12rem;
}
.title span:first-child {
  font-size: 0.17rem;
  color: #000;
  letter-spacing: 0;
  line-height: 0.17rem;
  height: 0.17rem;
}
.title span:last-child {
  font-size: 0.13rem;
  color: #888;
  letter-spacing: 0;
  line-height: 0.13rem;
  margin-left: 0.06rem;
}
.body {
  border-radius: 0.06rem;
  overflow: hidden;
  background: #fff;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.body .banner {
  flex: none;
  width: 100%;
  display: block;
  height: 0.95rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.body .banner > img {
  width: 100%;
  height: 100%;
}
.body .item_wrapper {
  width: 100%;
  flex: 1;
  padding-bottom: 0.12rem;
}
.body .item_wrapper .item_row {
  display: flex;
  width: 100%;
  padding: 0.06rem 0.03rem;
  box-sizing: border-box;
}
.body .item_wrapper .item_row .item {
  flex: 1;
  display: block;
  margin: 0 0.03rem;
  max-width: 33%;
  overflow: hidden;
  position: relative;
}
.body .item_wrapper .item_row .item .img_wrapper {
  padding-bottom: 100%;
  width: 100%;
  position: relative;
  background: #f9f9f9;
  border-radius: 0.04rem;
}
.body .item_wrapper .item_row .item .img_wrapper .img_container {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0.05rem;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}
.body .item_wrapper .item_row .item .img_wrapper img {
  width: 100%;
  height: 100%;
}
.body .item_wrapper .item_row .item .img_wrapper .tip {
  padding: 0 0.03rem;
  position: absolute;
  left: 0;
  bottom: 0;
  opacity: 0.8;
  background-image: linear-gradient(90deg, #1ad165 0, #05bb8e 100%);
  border-radius: 0.01rem 0.02rem;
  font-size: 0.12rem;
  color: #fff;
  line-height: 0.15rem;
  height: 0.15rem;
}
.body .item_wrapper .item_row .item .item_title {
  font-size: 0.13rem;
  color: #333;
  letter-spacing: 0;
  line-height: 0.13rem;
  margin: 0.06rem 0;
  padding: 0 0.01rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.body .item_wrapper .item_row .item .price_maker {
  font-size: 0.12rem;
  color: #ff2210;
  line-height: 0.12rem;
}
.body .item_wrapper .item_row .item .price {
  font-size: 0.14rem;
  color: #ff2210;
  line-height: 0.16rem;
  margin-left: -0.06rem;
}
.body .item_wrapper .item_row .item .src_price {
  font-size: 0.12rem;
  color: #999;
  line-height: 0.12rem;
  text-decoration: line-through;
}
