.searchbar {
  width: 100%;
  background: #fff;
  padding: 0.625rem 0;
}
.searchbar .topBar {
  display: flex;
  padding: 0 0.917rem 0 0.625rem;
  flex-direction: row;
}
.searchbar .topBar .left-block {
  align-items: center;
  display: flex;
}
.searchbar .topBar .left-block .back-icon {
  font-size: 1.583rem !important;
  color: #666;
}
@font-face {
  font-family: "iconfont";
  src: url("//at.alicdn.com/t/font_750634_zzudw0l58b.eot?t=1535713248181"); /* IE9*/
  src: url("//at.alicdn.com/t/font_750634_zzudw0l58b.eot?t=1535713248181#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */
      url("data:application/x-font-woff;charset=utf-8;base64,")
      format("woff"),
    url("//at.alicdn.com/t/font_750634_zzudw0l58b.ttf?t=1535713248181")
      format("truetype"),
    /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
      url("//at.alicdn.com/t/font_750634_zzudw0l58b.svg?t=1535713248181#iconfont")
      format("svg"); /* iOS 4.1- */
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.back-icon::before {
  content: "\e643";
}
.searchbar .topBar .search {
  display: flex;
  padding: 0 1.333rem 0 0.833rem;
  flex: 1;
  position: relative;
  max-width: 23.167rem;
  overflow: auto;
}
.searchbar .topBar .search .search-icon {
  width: 2.375rem;
  height: 2.417rem;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search .topBar .search .search-icon .sicon {
  color: #999;
  font-size: 1.083rem;
}
.sicon::before {
  content: "\e63d";
}
.searchbar .topBar .search .search-input {
  height: 2.417rem;
  flex: 1;
  font-size: 1.083rem;
  background: #f5f5f5;
  padding: 0 2.375rem;
  display: flex;
  border: none;
  border-radius: 1.208rem;
  outline: none;
}
.searchbar .topBar .search .search-icon-end {
  right: 1.208rem;
}
.search .topBar .search .search-icon .cicon {
  color: #d8d8d8;
  font-size: 1.25rem;
}
.cicon::before {
  content: "\e63e";
}
.searchbar .topBar .right-block-android .search-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4.667rem;
  height: 2.333rem;
  color: #fff;
  font-size: 1rem;
  background: #02b4a4;
  border-radius: 1.167rem;
}
