.top {
  padding-top: 0;
  position: fixed;
  display: flex;
  align-items: center;
  line-height: 50px;
  height: 50px;
  overflow: visible;
  z-index: 1;
  background: 0 0;
  width: 100%;
  left: 0;
  top: 0;
  text-align: center;
  font-size: 19px;
  color: #fff;
}
.top svg {
  fill: currentColor;
}
.top a {
  color: currentColor;
}
.top .back {
  flex: none;
  width: 10px;
  padding-left: 15px;
  margin-top: 3px;
  z-index: 1;
}
.top .title {
  fill: currentColor;
  flex: none;
  text-align: center;
  font-size: 19px;
  margin-left: 6px;
}
.search {
  flex: 1;
  width: 70%;
  z-index: 1;
  box-sizing: border-box;
}
.search a {
  display: flex;
  height: 29px;
  top: 6px;
  align-items: center;
  margin: 0 12px 0 6px;
  border-radius: 50px;
  background: rgba(255, 255, 255, 0.8);
}
.search a span {
  color: #aaa;
  font-size: 13px;
  margin-left: 10px;
  padding-top: 2px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  height: 50px;
  max-width: 111px;
}
.top .cart {
  flex: none;
  width: 21px;
  padding-right: 15px;
  margin-top: -4px;
  color: #fff;
  z-index: 1;
}
