li {
  list-style: none;
}
.detail-header-bg {
  height: 44px;
  margin-top: -44px;
}
.header-dom {
  position: fixed;
  z-index: 1;
  left: 0;
  right: 0;
  top: 0;
  line-height: 44px;
  overflow: visible;
}
.header-dom .main-dom {
  transition: background-color ease 0.3s;
  background: linear-gradient(rgba(0, 0, 0, 0.15, transparent));
}
.mui-flex {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
}
.header-dom .left-btns {
  padding: 7px 0 7px 10px;
  display: flex;
  flex-wrap: wrap;
}
.header-dom .left-btns a {
  margin-right: 5px;
}
.header-dom .icon-link {
  position: relative;
  height: 30px;
  width: 30px;
  display: block;
  transition: color ease 0.2s, background-color ease 0.2s, fill ease 0.2s;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.4);
}
.header-dom .icon-link svg {
  font-size: 20px;
  color: #fff;
  position: absolute;
  height: 20px;
  width: 20px;
  margin: 5px;
  text-align: center;
  fill: currentColor;
}
.header-dom .icon-link span {
  width: 0;
  height: 0;
  display: block;
  position: absolute;
  top: -400px;
}
.header-dom .header-nav {
  text-align: center;
  font-size: 13px;
  opacity: 0;
  transition: opacity ease 0.2s;
  margin: 0 8%;
  display: flex !important;
  flex-wrap: wrap;
}
.mui-flex .cell {
  flex: 1;
  width: 0;
  flex-basis: 0;
  max-width: 100%;
  padding: 0 !important;
  position: relative;
}
.header-dom .header-nav .active {
  color: #ff0036;
  border-color: #ff0036;
}
.header-dom .header-nav li {
  color: #999;
  height: 44px;
  line-height: 44px;
  flex: 1;
  border-bottom: 2px solid #fff;
}
.header-dom .right-btns {
  padding: 7px 10px 7px 0px;
  display: flex;
  flex-wrap: wrap;
}
.header-dom .right-btns a {
  margin-left: 5px;
}
