a {
  text-decoration: none !important;
}
.bottom-menu {
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 9000;
  font-display: Helvetica, sans-serif;
}
.bottom-menu-wrapper {
  display: flex;
  justify-content: space-between;
  height: 100%;
  text-align: center;
}
.bottom-menu .bottom-menu-wrapper .item {
  font-size: 10px;
  flex: 1;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.bottom-menu .show .native .item {
  padding-top: 0;
}
.bottom-menu .icon {
  margin-top: 0;
  width: 25px;
  height: 25px;
}
.bottom-menu .item span {
  margin-top: 0;
  display: block;
  font-size: 10px;
}
.bottom-menu .bottom-menu-wrapper .is-main {
  width: 55px !important;
  height: 55px !important;
  z-index: 2;
  margin: 0 auto;
  flex: none;
  -webkit-flex: none;
  background-size: 100% 100%;
}
.main-icon-contianer {
  display: inline-block;
  position: absolute;
  top: -20px;
  left: 50%;
  right: 0;
  transform: translateX(-50%);
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.1);
  width: 71px;
  height: 71px;
  z-index: -2;
  border-radius: 50%;
}
