.cate {
  margin-top: 12px;
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0 2px #ccc;
}
.title {
  display: flex;
  flex-direction: row;
  text-align: center;
  align-items: center;
  justify-content: center;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  overflow: hidden;
  position: relative;
}
.banner {
  width: 100%;
}
.title-name {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  text-align: center;
  line-height: 54px;
  color: #fff;
  font-size: 18px;
}
.cate-tabs-wrapper {
  padding-top: 20px;
  padding-bottom: 20px;
  margin-left: 10px;
  margin-right: 10px;
  overflow: auto;
  height: 20px;
}
.cate-tabs {
  margin: auto;
  height: 14px;
  background-color: #fff;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: no-wrap;
  -ms-flex-wrap: no-wrap;
  flex-wrap: no-wrap;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.cate-tab {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -moz-box-orient: horizontal;
  -moz-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.cate-tab .tab {
  color: #666;
  font-size: 14px;
  text-align: center;
  font-weight: 200;
  white-space: nowrap;
}
.cate-tab .tab.selected {
  color: #02b4a4;
  font-weight: 400;
}
.cate-tab::after {
  content: "";
  display: inline-block;
  width: 1px;
  background-color: #999;
  height: 12px;
  margin-left: 12.5px;
  margin-right: 12.5px;
}
.cate-tab:last-child::after {
  display: none;
}
.cate-items-content {
  position: relative;
  margin-top: 10px;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.item-conte-bg {
  width: 50%;
  margin-bottom: 15px;
  position: relative;
  background-color: #fff;
}
.item-conte {
  display: block;
}
.item-conte .img-bg {
  width: 100%;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
}
.img {
  width: 100%;
}
.item-conte .title-con {
  color: #333;
  font-size: 13px;
  text-align: left;
  line-height: 15px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-weight: 200;
  margin: 8px 10px 5px;
}
.item-conte .price-con {
  display: flex;
  flex-direction: row;
  align-content: flex-end;
  padding: 0 10px;
}
.item-conte .price-left {
  font-size: 11px;
  line-height: 13.5px;
  color: #ff4c44;
}
.item-conte .price-right {
  font-size: 15px;
  margin-left: 2px;
  line-height: 13.5px;
  color: #ff4c44;
}
.item-conte .sold-con {
  padding-top: 5px;
  margin-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  color: #999;
  font-size: 11px;
  line-height: 14px;
  text-overflow: ellipsis;
  font-weight: 200;
}
.item-conte .j_AddCart {
  position: absolute;
  bottom: 4px;
  right: 15px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #ff4c44;
}
.item-conte .cart-con {
  width: 18px;
  height: 17px;
}
