.module-container .module-title {
  background: #fff;
  font-weight: 400;
  height: 34px;
  line-height: 34px;
  position: relative;
  padding: 6px 0 0 12px;
  font-size: 14px;
  color: #666;
  border-left: 3px solid #ff0036;
}
.mui-standardItemProps {
  overflow: hidden;
  background-color: #f9f9f9;
}
.mui-standardItemProps .mui-standardItemProps-standardItemProps {
  padding: 15px 12px 0 18px;
}
.mui-standardItemProps li {
  margin-bottom: 12px;
  overflow: hidden;
  line-height: 1.4em;
}
.mui-standardItemProps li span:first-child {
  display: block;
  margin-right: 27px;
  width: 17.5%;
  color: #999;
}
.mui-standardItemProps li span {
  float: left;
  overflow: hidden;
  font-size: 14px;
  color: #051b28;
}
.mui-standardItemProps .mui-standardItemProps-morewraper {
  background-color: #fff;
  text-align: center;
}
.mui-standardItemProps
  .mui-standardItemProps-morewraper
  .mui-standardItemProps-more {
  display: inline-block;
  margin: 0 auto;
  height: 50px;
  line-height: 50px;
  font-size: 14px;
  color: #666;
  text-align: center;
}
