a {
  text-decoration: none !important;
}
img {
  vertical-align: middle;
  border: none;
}
.content .wrapper {
  margin-left: 10px;
  margin-right: 10px;
  padding-top: 22px;
  padding-bottom: 6px;
  border-radius: 6px;
}
.content .title {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  margin-bottom: 10px;
  margin-left: 9px;
  overflow: visible;
}
.content .title-main {
  flex: none;
  font-size: 17px;
  overflow: visible;
  line-height: 17px;
  font-weight: 700;
}
.content .count-down {
  margin-left: 5px;
  display: flex;
  align-items: baseline;
  float: 1;
}
.content .rows {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-left: 6px;
  margin-right: 6px;
}
.content .row-item {
  width: 32%;
  min-width: 32%;
  box-sizing: border-box;
  margin-bottom: 6px;
  background: #fff;
  border-radius: 2px;
  padding: 6px;
  margin-right: 4px;
}
.content .row-item a {
  position: relative;
}
.content .image-wrapper {
  position: relative;
  padding: 5px;
}
.content .row-item-image-3 {
  margin: 0 auto;
}
.content .row-3 {
  width: 100%;
}
.content .item-icon {
  padding: 0 4px;
  opacity: 0.8;
  background: #fff4f3;
  font-size: 11px;
  color: #ff2210;
  letter-spacing: 0;
  height: 16px;
  line-height: 16px;
  position: absolute;
  left: 0;
  bottom: -3px;
}
.content .item-icon::before {
  content: "";
  position: absolute;
  left: 0;
  border: 1px solid #ff4c44;
  border-radius: 4px;
  width: 200%;
  height: 200%;
  transform-origin: 0 0;
  transform: scale(0.5, 0.5);
  box-sizing: border-box;
}
.content .row-item-title {
  font-size: 13px;
  line-height: 17px;
  height: 16px;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: 6px;
  text-align: left;
}
.content .row-item-tag {
  justify-content: flex-start;
  margin-top: 4px;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  flex-wrap: nowrap;
  height: 15px;
  align-items: center;
}
.content .tag {
  color: #ff2210;
  border: none;
  font-size: 12px;
  font-weight: 300;
  padding-top: 0;
  padding-bottom: 0;
  text-align: left;
  align-items: center;
}
.content .row-item-price {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: flex-start;
  margin-top: 5px;
}
.content .price-mark {
  font-size: 12px;
  color: #333;
  font-family: Arial;
  margin-right: 2px;
}
.content .price {
  font-size: 14px;
  line-height: 14px;
  color: #333;
}
.content .origin-price {
  font-size: 10px;
  color: #999;
  text-decoration: line-through;
  margin-left: 4px;
  font-family: Arial;
}
