#s-actionBar-container {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  transform: translateZ(0);
}
#s-actionBar-container .action-bar-wrap {
  height: 50px;
  background-color: #fff;
  line-height: 50px;
  display: flex;
  text-align: center;
  padding-bottom: var(--safe-area-inset-bottom);
}
#s-actionBar-container .action-bar-wrap .cell .toshop {
  max-width: 50px;
  min-width: 13%;
}
#s-actionBar-container .action-bar-wrap .cell {
  display: block;
  box-sizing: border-box;
  flex: 1;
  height: 50px;
  color: #999;
  line-height: normal;
  font-size: 12px;
  padding-top: 4px;
}
#s-actionBar-container .action-bar-wrap .cell.toshop svg {
  color: #999;
}
#s-actionBar-container .action-bar-wrap .cell.support svg {
  color: #4a90e2;
}
#s-actionBar-container .action-bar-wrap .cell svg {
  width: 22px;
  height: 22px;
  display: block;
  margin: 4px auto 0;
  fill: currentColor;
}
.action-bar-wrap .trade {
  flex: 1;
  display: flex;
  text-align: center;
  transform: translate3d(0, 0, 0);
}
.trade {
  min-width: 180px;
}
.trade .cart {
  flex: 1;
  color: #fff;
  background-color: #ff9500;
  font-size: 15px;
  line-height: 34px;
}
.action-bar-wrap .cart {
  background: #ff9500;
}
.trade a p:first-child:last-child {
  line-height: 34px;
  font-size: 15px;
}
.trade .info {
  flex: 1;
  justify-content: center;
  align-items: center;
  word-break: break-all;
  flex-direction: column;
  display: none;
}
.trade a {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 8px 0;
  line-height: 34px;
}
.trade .buy {
  flex: 1;
  color: #fff;
  background-color: #ff0036;
  font-size: 15px;
  line-height: 34px;
}
.trade a p:first-child:last-child {
  line-height: 34px;
  font-size: 15px;
}
/* ItemSlider */
img {
  vertical-align: middle;
  border: none;
}
.module-preview {
  position: relative;
}
.module-preview .preview-slider {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.module-preview .preview-slider .scroller {
  will-change: transform;
  height: 100%;
}
.mui-slider .scroller {
  display: flex;
}
.module-preview .preview-slider .item {
  position: relative;
  display: block;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}
.mui-slider .item {
  flex-shrink: 0;
  width: 100%;
}
.module-preview .preview-slider .item img {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  width: 100vw;
  min-height: 100%;
  object-fit: contain;
}
.module-preview .preview-slider .item::after {
  display: block;
  content: "";
  padding-top: 100%;
}
.module-preview .nav {
  position: absolute;
  text-align: center;
  left: 30px;
  right: 30px;
  bottom: 10px;
}
.module-preview .nav .current {
  background-color: #aaa;
  opacity: 1;
  color: #fff;
}
.module-preview .nav i {
  border-radius: 4.5px;
  margin: 0 4px;
  color: #000;
  height: 0;
  display: block;
}
.module-preview .nav .pagination {
  position: absolute;
  bottom: 0;
  right: -20px;
  background-color: rgba(100, 100, 100, 0.6);
  border-radius: 10px;
  padding: 0 10px;
  line-height: 20px;
  height: 20px;
  color: #fff;
}
